import * as Msal from 'msal';
import * as microsoftTeams from '@microsoft/teams-js';

export class Auth
{
    public signIn()
    {
        microsoftTeams.initialize();
        const scopes = ["offline_access", "profile", "User.ReadBasic.All", "mail.send"];
                
        const callback = (errorDesc: string, token: string, error: string, tokenType: string) =>
        {

        };

        let userAgentApplication = new Msal.UserAgentApplication("530d427b-f6b7-4cb1-a452-1d4c360108f2", null, callback, 
        {   });

        userAgentApplication.loginPopup(scopes).
        then(
            idToken => {
                console.log("aquire token", idToken);
                userAgentApplication.acquireTokenSilent(scopes).then(token => {
                console.log("final", token);
                microsoftTeams.authentication.notifySuccess(token);
            })
            .catch(err => {
                microsoftTeams.authentication.notifyFailure(err);
            });
        });
    }
}


